<template>
    <el-card class="portrait-main" shadow="hover">
        <div slot="header" class="clearfix">
            <i class="el-icon-s-custom" style="font-size: larger"></i>
            <span style="font-size: larger;font-weight: bold"> (Patient Portrait)</span>
        </div>
        <div class="portrait-content">
<!--            <ul class="infinite-list" v-infinite-scroll="load" style="overflow:auto">-->
                <el-row >
                    <el-col :span=24 class="tip"></el-col>
                </el-row>
                <el-descriptions title="" column="2" border size="mini;" labelStyle="word-break:keep-all;width:80px" contentStyle="max-height:20px">
                    <el-descriptions-item label="">{{portrait.age}}</el-descriptions-item>
                    <el-descriptions-item label="" v-if="portrait.sex==='0'"class="basic_item"></el-descriptions-item>
                    <el-descriptions-item label="" v-else-if="portrait.sex==='1'" class="basic_item"></el-descriptions-item>
                    <el-descriptions-item label="">{{portrait.marriage}}</el-descriptions-item>
                    <el-descriptions-item label=""><el-tag>{{portrait.profession}}</el-tag></el-descriptions-item>
                    <el-descriptions-item label="" span="2" v-if="portrait.reason!==''">{{portrait.reason}}</el-descriptions-item>
                    <el-descriptions-item label="" span="2">
<!--                        <ul class="infinite-list" v-infinite-scroll="load" style="overflow:auto;height: 100px">-->
                            <div v-for="(item,i) in portrait.description">
                                <p v-if="item.content!=''">{{i}}. {{item.content}}</p>
                            </div>
<!--                        </ul>-->
                    </el-descriptions-item>
                    <el-descriptions-item label="" span="2" v-if="relate_disease.length!==0">
                        <div v-for="(item,i) in relate_disease" >
                            <p class="sub_item" v-if="item!=''">{{i}}. {{item}}</p>
                        </div>
                    </el-descriptions-item>
                    <el-descriptions-item label="" span="2" v-else>
                        
                    </el-descriptions-item>
                </el-descriptions>
        </div>
    </el-card>


</template>

<script>
    import {baseUrl} from "../util";
    import axios from "axios";

    export default {
        name: "Portrait",
        info: "",
        props: {
            userId: Number
        },
        data(){

            return {
                portrait: '',
                relate_disease:[],
            }
        },
        created() {
            // this.init()
        },
        mounted:async function(){
            this.init()

        },
        methods:{
            beforeUpdate :function(){
            },
            init(){
                let url = baseUrl + "/getPortrait?userId="+this.userId;
                axios
                    .get(url)
                    .then((res) => {
                        this.portrait = JSON.parse(res.data.content);
                        this.$set({
                            portrait: res.data.content
                        });
                        console.log(this.portrait);
                        let items = this.portrait.items;
                        if (parseInt(items[9].content)>0){
                            this.relate_disease.push("")
                        }
                        if(parseInt(items[10].content)>0){
                            this.relate_disease.push("6")
                        }
                        if(parseInt(items[11].content)>0){
                            this.relate_disease.push("")
                        }
                        if(parseInt(items[12].content)>0){
                            this.relate_disease.push("")
                        }
                        if(parseInt(items[13].content)>0){
                            this.relate_disease.push("")
                        }
                        console.log(items);
                        console.log(this.relate_disease)
                    })
                    .catch((err) => {
                        console.log(err);
                    });


            }
        },
    }
</script>

