<template>
    <div style="display: flex;flex-direction: column;justify-content: center;width: 20rem;">
        <div style="font-family: 'Helvetica Neue';font-size: large;display:flex;justify-content: center;"></div>
        <div class="subTitle" ></div>
        <div id="radar_canvas" class="echart" style="width: 300px;height: 240px;margin-top: 1rem"></div>
        <div class="subTitle"></div>
        <el-row :gutter="20" class="content1">
            <el-col :span="12" style="display: flex;flex-direction:row">: <div style="color: #9abdf9">{{dRisks}}</div></el-col>
            <el-col :span="12" style="display: flex;flex-direction:row">: <div style="color: #9abdf9">{{sRisks}}</div></el-col>
        </el-row>
        <div v-if="riskText" class="content1">{{riskText}}</div>
        <div v-for="(item,index) of this.description" >
            <div v-if="item.content!==''" class="content2">
                <div style="width: 0.7rem;height: 0.7rem;border-radius: 30px;background-color: #8fc5f8;margin-top:0.3rem;margin-right:0.4rem"/>
                {{item.content}}
            </div>
        </div>
        <div v-if="relationText" class="subTitle"></div>
        <div class="content">{{relationText}}</div>
        <div class="subTitle"></div>
        <div class="content"></div>
    </div>
</template>

<script>
    const relates = new Map([[0,""],[1,""],[2,""],[3,""],[4,""]]);
    export default {
        name: 'Report',
        props: {
            items: Array,
            description: Array
        },
        data() {
            var charts;
            return {
                dRisks:'',
                sRisks:'',
                relationText:null,
                riskText:null,
                charts:null
            }
        },
        // mounted() {
        //     this.drawLine()
        // },
        methods: {
            initRadar(input) {
                if (this.charts != null && this.charts !== "" && this.charts !== undefined) {
                    this.charts.dispose();//
                }
                this.charts = this.$echarts.init(document.getElementById('radar_canvas'));
                var option = {
                    radar: {
                        shape: 'circle',
                        indicator: [
                            // { name: '', max: 4 },
                            // { name: '', max: 8 },
                            // { name: '', max: 6 },
                            // { name: '', max: 3 },
                            // { name: '', max: 4 },
                            // { name: '', max: 6 },
                            // { name: '',max: 4},
                            // { name: '', max: 3 },

                            { name: '', max: 4 },
                            { name: '', max: 3 },
                            { name: '',max: 4},
                            { name: '', max: 6 },
                            { name: '', max: 4 },
                            { name: '', max: 3 },
                            { name: '', max: 6 },
                            { name: '', max: 8 },
                        ]
                    },
                    series: [
                        {
                            name: 'Budget vs spending',
                            type: 'radar',
                            areaStyle: {
                              color: '#81acf8'
                            },
                            lineStyle: {
                                color: '#81acf8'
                            },
                            color:'#81acf8',
                            data: [
                                {
                                    value: input,
                                    // name: 'Allocated Budget'
                                },
                            ]
                        }
                    ]
                };
                this.charts.setOption(option);
                window.addEventListener("resize", () => { this.charts.resize();});
            },

            getSymptom(){
                let syms = [];
                for (let i = 2; i < 8; i++) {
                    let val1 = 0;
                    val1 = parseInt(this.items[i].content);
                    if((val1>0&&i!==3&&i!==7)||(val1>1&&i===3)||(val1>4&&i===7)){
                        syms.push(this.items[i]);
                    }
                }
                return syms;
            },

            getRisk(){
                let syms = this.getSymptom();
                console.log("getRisk: begin",syms);
                let deRiskValue = 0;
                let suRiskValue = 0;
                let riskText="";
                let levelText="";
                let suicideText="";
                let  p1=0,p2=0,p8=0;

                p1 =parseInt(this.items[0].content);
                p2 = parseInt(this.items[1].content);
                p8 = parseInt(this.items[7].content);

                if(p1>1&&p2>0){
                    switch (syms.length){
                        case 0: case 1: case 2:
                            deRiskValue = 2;
                            break;
                        case 3:
                            deRiskValue = 3;
                            break;
                        case 4: case 5:
                            deRiskValue = 4;
                            break;
                        case 6:
                            deRiskValue = 5;
                            break;
                        default:
                            break;
                    }
                }else if(p1>1||p2>0){
                    switch (syms.length){
                        case 0: case 1: case 2:
                            deRiskValue = 1;
                            break;
                        case 3:
                            deRiskValue = 2;
                            break;
                        case 4:
                            deRiskValue = 3;
                            break;
                        case 5:
                            deRiskValue = 4;
                        case 6:
                            deRiskValue = 5;
                            break;
                        default:
                            break;
                    }
                }
                if((p1>3||p2>2)&&deRiskValue!==5){
                    deRiskValue = deRiskValue+1;
                }
                if(p8>=7&&deRiskValue<4){
                    deRiskValue = 4;
                }else if(p8>4&&deRiskValue<3){
                    deRiskValue = 3;
                }
                switch (p8){
                    case 0: case 1: case 2:
                        suRiskValue = 0;
                        break;
                    case 3: case 4:
                        suRiskValue = 1;
                        break;
                    case 5: case 6:
                        suRiskValue = 2;
                        break;
                    case 7:
                        suRiskValue = 3;
                        break;
                    case 8:
                        suRiskValue = 4;
                        break;
                    default:
                        break;
                }

                switch (deRiskValue) {
                    case 0:
                        this.dRisks = "";
                        break;
                    case 1:
                        this.dRisks = "";
                        break;
                    case 2:
                        this.dRisks = "";
                        break;
                    case 3:
                        this.dRisks = "";
                        break;
                    case 4:
                        this.dRisks = "";
                        break;
                    case 5:
                        this.dRisks = "";
                        break;
                }
                console.log();
                switch (suRiskValue) {
                    case 0:
                        this.sRisks = "";
                        break;
                    case 1:
                        this.sRisks = "";
                        break;
                    case 2:
                        this.sRisks = "";
                        break;
                    case 3:
                        this.sRisks = "";
                        break;
                    case 4:
                        this.sRisks = "";
                        break;
                }
                if(syms.length!==0){
                    this.riskText = "";
                }

            },

            getRelated: function () {
                let relation = [];
                for (let i = 9; i < 14; i++) {
                    let val1 = 0;
                    val1 = parseInt(this.items[i].content);
                    if (val1 > 0) {
                        relation.push(relates.get(i - 9));
                    }
                }
                if (relation.length !== 0) {
                    this.relationText = "";
                    for (let i = 0;i < relation.length;i++)
                    {
                        if (i !== relation.length - 1)
                            this.relationText = this.relationText + relation[i] + ',';
                        else this.relationText = this.relationText + relation[i] + "";
                    }
                }
            }
        },
        created() {
            this.$nextTick(function () {
                let data=[];
                this.items.forEach((e)=>{
                    data.push(e.content)
                });
                this.initRadar(data)
            })
        },
        mounted() {
            let data=[];
            this.items.forEach((e)=>{
                data.push(e.content)
            });
            this.initRadar(data);
            this.getRisk();
            this.getRelated();
        }
    }
</script>

<style>
    .subTitle{
        margin-top: 1rem;
        color: #6c6c6c;
        background-color: #8fc5f8;
        padding: 0.3rem;
        border-radius: 0px 10px 10px 0px;
    }
    .content{
        margin-top: 0.5rem;
        font-size: smaller
    }
    .content1{
        margin-top: 0.6rem;
    }
    .content2{
        margin-top: 0.4rem;
        font-size: smaller;
        display: flex;
        flex-direction: row;
        /*justify-content: center;*/
        /*align-items: center;*/
    }
    .list{
        display: flex;
        flex-direction: column;
        justify-content: flex-start;
        align-items: flex-start;
    }
</style>